.grid12 {
  display: grid;
  grid-template-columns: [center-start] repeat(12, [col-start] minmax(0px, 73px) [col-end]) [center-end];
  column-gap: 24px;
  align-items: center;
}

.grid12-5 {
  grid-column: span 5;
}

.grid12-6 {
  grid-column: span 6;
}

.grid12-7 {
  grid-column: span 7;
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  80% {
    transform: translateX(1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(10rem);
  }
  80% {
    transform: translateX(-1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 16px;
  box-sizing: border-box;
}

body {
  box-sizing: border-box;
}

p {
  padding-bottom: 40px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0.5em auto;
}

.container {
  max-width: 1140px;
  margin: auto;
}

img {
  width: 100%;
}

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.5;
  color: #000;
}

p,
a {
  font-size: 1rem;
}

strong {
  font-size: 1.25em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 900;
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0 auto 0.75em auto;
}
h1 {
  font-size: 4.768em;
}
h2 {
  font-size: 3.815em;
}
h3 {
  font-size: 3.052em;
}
h4 {
  font-size: 2.441em;
}
h5 {
  font-size: 1.953em;
}
h6 {
  font-size: 1.563em;
}

.text-large {
  font-size: 1.25rem;
}
.text-bold {
  font-weight: 700;
}

.btn {
  font-size: 1.25em;
}

.text-center {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.p-top--small {
  padding-top: 1.5rem;
}
.p-top--medium {
  padding-top: 4rem;
}
.p-top--big {
  padding-top: 8rem;
}
.p-bottom--small {
  padding-bottom: 1.5rem;
}
.p-bottom--medium {
  padding-bottom: 4rem;
}
.p-bottom--big {
  padding-bottom: 8rem;
}

.header {
  height: 70px;
}

.btn {
  display: inline-flex;
  position: relative;
  align-items: center;
  justify-content: center;
  height: 2.5em;
  padding: 0 1em;
  transition: background-color 0.5s, box-shadow 0.5s;
  border: 0;
  border-radius: 8px;
  outline: 0;
  text-decoration: none;
  color: inherit;
  border: solid 0.25px black;
  box-shadow: 0 3px 10px rgba(27, 26, 27, 0.2);
  font-size: 1.25em;
  font-weight: bold;
  cursor: pointer;
}
.btn:hover {
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}
.btn:active, .btn:focus {
  outline: none;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}
.btn--fill {
  background-color: #009fe3;
  color: #fff;
  border: solid 2px #009fe3;
}
.btn--empty {
  background-color: none;
  color: #009fe3;
  border: solid 2px #009fe3;
}
.btn--underline {
  background-color: none;
  color: #000;
  border: none;
  box-shadow: none;
  text-decoration: underline;
  text-underline-offset: 8px;
}

.link {
  display: inline-flex;
  position: relative;
  align-items: center;
  justify-content: center;
  height: 2.5em;
  padding: 0 1em;
  transition: background-color 0.5s, box-shadow 0.5s;
  border: 0;
  border-radius: 8px;
  outline: 0;
  text-decoration: none;
  color: inherit;
  border: solid 0.25px black;
  box-shadow: 0 3px 10px rgba(27, 26, 27, 0.2);
  font-size: 1.25em;
  font-weight: bold;
  cursor: pointer;
}
.link--underline {
  background-color: none;
  color: #000;
  border: none;
  box-shadow: none;
  text-decoration: underline;
  text-underline-offset: 8px;
}

.module.textbox .textbox__text {
  grid-column: col-start 2/col-end 11;
}

.textbox__buttons {
  display: grid;
  grid-auto-columns: 1fr;
  grid-auto-flow: column;
  max-width: fit-content;
  margin: auto;
  column-gap: 24px;
  row-gap: 1em;
  padding-bottom: 1em;
}
.textbox__buttons .link {
  grid-row: 2;
}

.module.textbox.bg-blue, .module.textbox.bg-blue-gradient,
.textbox .container.bg-blue,
.textbox .container.bg-blue-gradient {
  color: #fff;
}
.module.textbox.bg-blue .btn--fill, .module.textbox.bg-blue-gradient .btn--fill,
.textbox .container.bg-blue .btn--fill,
.textbox .container.bg-blue-gradient .btn--fill {
  background-color: #fff;
  color: #009fe3;
  border: solid 2px #fff;
}
.module.textbox.bg-blue .btn--empty, .module.textbox.bg-blue-gradient .btn--empty,
.textbox .container.bg-blue .btn--empty,
.textbox .container.bg-blue-gradient .btn--empty {
  color: #fff;
  border: solid 2px #fff;
}
.module.textbox.bg-blue .link--underline, .module.textbox.bg-blue-gradient .link--underline,
.textbox .container.bg-blue .link--underline,
.textbox .container.bg-blue-gradient .link--underline {
  color: #fff;
}
.module.textbox.bg-blue,
.textbox .container.bg-blue {
  background-color: #009fe3;
}
.module.textbox.bg-blue-gradient,
.textbox .container.bg-blue-gradient {
  background: linear-gradient(180deg, #009fe3 0%, #083f74 100%);
}

.height--90vh {
  height: calc(90vh - 70px);
}
.height--75vh {
  height: calc(75vh - 70px);
}
.height--50vh {
  height: calc(50vh - 70px);
}

.teaser {
  position: relative;
}
.teaser__text > * {
  grid-column: 1/-1;
}
.teaser__text > * h1,
.teaser__text > * h2,
.teaser__text > * h3,
.teaser__text > * h4,
.teaser__text > * h5,
.teaser__text > * h6 {
  margin: 0;
}
.teaser__buttons {
  display: grid;
  grid-auto-columns: 1fr;
  grid-auto-flow: column;
  max-width: fit-content;
  margin: auto;
  column-gap: 24px;
  row-gap: 1em;
  padding-bottom: 1em;
}
.teaser__buttons .link {
  grid-row: 2;
}
.teaser__buttons .btn,
.teaser__buttons .link {
  font-size: 1.125em;
  height: 2.25em;
}
.teaser__bg-image {
  opacity: 1;
  background-size: cover;
  background-position: center center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -2;
}
.teaser__bg-gradient {
  background-size: cover;
  background-position: center center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}
.teaser.tc--blue-light {
  color: #009fe3;
}
.teaser.tc--white {
  color: #fff;
}
.teaser.tc--white .btn--fill {
  background-color: #fff;
  color: #009fe3;
  border: solid 2px #fff;
}
.teaser.tc--white .btn--empty {
  color: #fff;
  border: solid 2px #fff;
}
.teaser.tc--white .link--underline {
  color: #fff;
}

.imagetext > * > * {
  grid-row: 1;
  height: fit-content;
}
.imagetext img {
  width: 100%;
}
.imagetext .imagetext__textbox h5 {
  color: #009fe3;
}
.imagetext .btn,
.imagetext .link {
  font-size: 1.125em;
  height: 2.25em;
}
.imagetext.bg-blue {
  background-color: blue;
  color: #fff;
}
.imagetext.bg-blue .btn--fill {
  background-color: #fff;
  color: #009fe3;
  border: solid 2px #fff;
}
.imagetext.bg-blue .btn--empty {
  color: #fff;
  border: solid 2px #fff;
}
.imagetext.bg-blue .link--underline {
  color: #fff;
}
.imagetext.bg-blue.box-design-on .container {
  background-color: blue;
}
.imagetext.bg-blue.box-design-on .image-first .imagetext__image {
  margin-left: 10%;
}
.imagetext.bg-blue.box-design-on .image-first .imagetext__textbox {
  margin-right: 10%;
}
.imagetext.bg-blue.box-design-on .text-first .imagetext__textbox {
  margin-left: 10%;
}
.imagetext.bg-blue.box-design-on .text-first .imagetext__image {
  margin-right: 10%;
}
.imagetext .image-first .imagetext__image {
  order: 1;
}
.imagetext .image-first .imagetext__textbox {
  margin-left: 5%;
  order: 2;
}
.imagetext .image-first.ratio--6-6 .imagetext__image {
  grid-column: span 6;
}
.imagetext .image-first.ratio--6-6 .imagetext__textbox {
  grid-column: span 6;
}
.imagetext .image-first.ratio--5-7 .imagetext__image {
  grid-column: span 5;
}
.imagetext .image-first.ratio--5-7 .imagetext__textbox {
  grid-column: span 7;
}
.imagetext .image-first.ratio--7-5 .imagetext__image {
  grid-column: span 7;
}
.imagetext .image-first.ratio--7-5 .imagetext__textbox {
  grid-column: span 5;
}
.imagetext .text-first .imagetext__textbox {
  margin-left: 0;
  margin-right: 5%;
}
.imagetext .text-first--6-6 .imagetext__textbox {
  grid-column: 1/span 6;
}
.imagetext .text-first--6-6 .imagetext__image {
  grid-column: span 6/-1;
}
.imagetext .text-first--5-7 .imagetext__textbox {
  grid-column: 1/span 5;
}
.imagetext .text-first--5-7 .imagetext__image {
  grid-column: span 7/-1;
}
.imagetext .text-first--7-5 .imagetext__textbox {
  grid-column: 1/span 7;
}
.imagetext .text-first--7-5 .imagetext__image {
  grid-column: span 5/-1;
}

.tiles .container {
  display: grid;
  grid-auto-columns: minmax(0, 1fr);
  grid-auto-flow: column;
  gap: 24px;
}
.tiles .tile__wrapper {
  padding: 10%;
  display: grid;
  grid-template-rows: auto minmax(0px, 300px);
}
.tiles .image-first .tile__image {
  order: 1;
}
.tiles .image-first .tile__content {
  order: 2;
}
.tiles .text-first .tile__content {
  order: 1;
}
.tiles .text-first .tile__image {
  order: 2;
}
.tiles .heading {
  color: #009fe3;
}
.tiles .textbox__buttons {
  grid-auto-flow: row;
}