.module.textbox {
  // structure
  .textbox__text {
    grid-column: col-start 2 / col-end 11;
  }
}

// general textbox style. It is used for another blocks
.textbox__buttons {
  display: grid;
  grid-auto-columns: 1fr;
  grid-auto-flow: column;
  max-width: fit-content;
  margin: auto;
  column-gap: 24px;
  row-gap: 1em;
  padding-bottom: 1em;
  .link {
    grid-row: 2;
  }
}

// background color
.module.textbox,
.textbox .container {
  &.bg-blue,
  &.bg-blue-gradient {
    color: $color-white;
    .btn--fill {
      background-color: $color-white;
      color: $color-blue-light;
      border: solid 2px $color-white;
    }
    .btn--empty {
      color: $color-white;
      border: solid 2px $color-white;
    }
    .link--underline {
      color: $color-white;
    }
  }
  &.bg-blue {
    background-color: $color-blue-light;
  }
  &.bg-blue-gradient {
    background: linear-gradient(
      180deg,
      $color-blue-light 0%,
      $color-blue-dark2 100%
    );
  }
}
